<template>
  <div class="loginWrapper">
    <img class="login_bg" src="../assets/img/login.jpg">
    <div class="loginCardWrapper">
      <Card class="loginCard">
        <div class="logo">
          <router-link to="/home">
            <img src="../assets/img/logo.jpg">
          </router-link>
        </div>
        <div>
          <h2>了解世界,从妙漫开始</h2>
        </div>
        <!--登录-->
        <div v-show="flag" class="login_page">
          <div v-show="loginType">
            <!--登录-->
            <div v-show="loginType" class="login_password">
              <div class="inputWrapper">
                <div class="inputContent">
                  <Icon class="inputIcon" type="ios-telephone"></Icon>
                  <input class="inputCss" v-model="phone" type="text" placeholder="输入手机号码...">
                </div>
                <div class="inputContent">
                  <div @click="showPasswrod" v-show="inputFlag">
                    <Icon class="inputIcon inputHover" type="eye-disabled"></Icon>
                  </div>
                  <div @click="showPasswrod" v-show="!inputFlag">
                    <Icon class="inputIcon inputHover" type="eye"></Icon>
                  </div>
                  <input v-model="password" @keyup.enter="verify();login()" class="inputCss"
                         :type="inputType" placeholder="输入密码...">
                </div>
              </div>
            </div>
            <div class="description">
              <div @click="forget" class="forget">
                <a>忘记密码?</a>
              </div>
            </div>
            <div class="loginButton">
              <Button type="primary" @click="verify();login()">登录</Button>
            </div>
            <!--<div class="loginButton">
              <Button type="primary" @click="gg">登录</Button>
            </div>-->
          </div>

          <div v-show="!loginType">
            <!--忘记密码-->
            <div v-show="forgetPassword">
              <div class="inputWrapper">
                <div class="inputContent">
                  <Icon class="inputIcon" type="ios-telephone"></Icon>
                  <input class="inputCss" v-model="phone" type="text" placeholder="输入手机号码...">
                </div>
                <div class="inputContent">
                  <div @click="showPasswrod" v-show="inputFlag">
                    <Icon class="inputIcon inputHover" type="eye-disabled"></Icon>
                  </div>
                  <div class="inputContent">
                    <div class="inputButton">
                      <Button type="ghost" @click="getCode" :disabled="disabled">{{timeCode}}</Button>
                    </div>
                    <input v-model="codeForget" class="inputCss" type="text" placeholder="输入验证码...">
                  </div>
                </div>
                <div class="loginButton">
                  <Button type="primary" @click="verify();reset()">下一步</Button>
                </div>
              </div>
            </div>
            <!--下一步-->
            <div v-show="!forgetPassword">
              <div>
                <div class="inputWrapper">
                  <!--重置密码-->
                  <div class="inputContent">
                    <div @click="showPasswrod" v-show="inputFlag">
                      <Icon class="inputIcon inputHover" type="eye-disabled"></Icon>
                    </div>
                    <div @click="showPasswrod" v-show="!inputFlag">
                      <Icon class="inputIcon inputHover" type="eye"></Icon>
                    </div>
                    <input v-model="password2" class="inputCss" :type="inputType" placeholder="输入密码...">
                  </div>
                  <!--确认密码-->
                  <div class="inputContent">
                    <div @click="showPasswrod" v-show="inputFlag">
                      <Icon class="inputIcon inputHover" type="eye-disabled"></Icon>
                    </div>
                    <div @click="showPasswrod" v-show="!inputFlag">
                      <Icon class="inputIcon inputHover" type="eye"></Icon>
                    </div>
                    <input v-model="password3" class="inputCss" :type="inputType" placeholder="确认密码...">
                  </div>
                  <div class="loginButton">
                    <Button type="primary" @click="verify();OK()">完成</Button>
                  </div>
                </div>
              </div>

            </div>
          </div>

          <div class="otherLogin">
            <div class="title">
              <p>其他方式登录:</p>
            </div>
            <div>
              <a href="https://news.immnc.com/oauth/qq/redirect">
              <div class="other_login_icon">
                <img src="../assets/img/icon/qq.svg">
                <p>QQ登录</p>
              </div>
              </a>
              <a href="https://news.immnc.com/api/user/qrAuthorize?returnUrl=http://www.immnc.com/#/weinxinLogin">
              <div class="other_login_icon">
                <img src="../assets/img/icon/weixin.svg">
                <p>微信登录</p>
              </div>
              </a>
            </div>
          </div>
          <div class="register">
            <div class="rightWrapper">
              <span>没有账号?</span>
              <a @click="go">注册</a>
            </div>
          </div>
        </div>
        <!--注册-->
        <div v-show="!flag" class="register_page">
          <div class="inputWrapper">
            <div class="inputContent">
              <Icon class="inputIcon" type="ios-telephone"></Icon>
              <input class="inputCss" v-model="phone" type="text" placeholder="输入手机号码...">
            </div>
            <div class="inputContent">
              <div class="inputButton">
                <Button type="ghost" @click="getCode" :disabled="disabled">{{timeCode}}</Button>
              </div>
              <input v-model="codeRegister" class="inputCss" type="text" placeholder="输入验证码...">
            </div>
            <div class="inputContent">
              <div @click="showPasswrod" v-show="inputFlag">
                <Icon class="inputIcon inputHover" type="eye-disabled"></Icon>
              </div>
              <div @click="showPasswrod" v-show="!inputFlag">
                <Icon class="inputIcon inputHover" type="eye"></Icon>
              </div>
              <input v-model="password4" class="inputCss" :type="inputType" placeholder="输入密码...">
            </div>
            <div class="description">
              <p>注册即代表同意《益豪娱创协议》《隐私政策》</p>
            </div>
            <div class="loginButton">
              <Button type="primary" @click="verify();register()">注册</Button>
            </div>
            <div class="login">
              <div class="rightWrapper">
                <a @click="go">第三方登录</a>
                <span>已有账号?</span>
                <a @click="go">登录</a>
              </div>
            </div>
          </div>
        </div>
      </Card>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import {saveToLocal,loadURL} from "../../src/common/js/store.js"

  const PRIFIX = "https://news.immnc.com/"
  /*const PRIFIX = "http://192.168.100.2:8088"*/
  /*const PRIFIX = "http://192.168.0.4:8080"*/

  export default {
    data() {
      return {
        inputType: "password",
        inputFlag: true,
        forgetPassword: true,
        flag: true,
        loginType: true,
        disabled: false,
        verifyFlag: false,
        time: 60,
        timeCode: "获取验证码",
        phone: "",
        password: "",
        password2: "",
        password3: "",
        password4: "",
        codeForget: "",
        codeRegister: "",
      }
    },
    methods: {
      login() {
        var qs = require('qs')
        var self = this
        this.$http.post(PRIFIX + '/api/user/loginAndPhonePassword', qs.stringify({
          'phone': this.phone,
          'password': this.password
        })).then(function (response) {
          response = response.data
          if (response.code == 0) {
            let user = response.data.userVO
            saveToLocal(
              user.userId,
              user.nikeName,
              user.avatar,
              user.qq,
              user.sex,
              user.email,
              user.city,
              user.birthday,
              user.signature,
              user.backgroundImg,
              user.phone,
              user.fansCounts,
              user.followCounts,
              true
            )
            self.$Notice.success({
              title: "登录成功",
            })
            var u = loadURL()
            if(u == '' || u == null) {
              setTimeout(function () {
                self.$router.push('/home')
              },1000)
            } else {
              setTimeout(function () {
                window.location.href = u
              },1000)
            }

          } else {
            self.$Notice.error({
              title: response.message,
            })
          }
        }).catch(function (error) {})
      },
      showPasswrod() {
        this.inputFlag = !this.inputFlag
        if (this.inputFlag) {
          this.inputType = "password"
        } else {
          this.inputType = "text"
        }
      },
      go() {
        this.inputType = "password"
        this.inputFlag = true
        this.forgetPassword = true
        this.flag = !this.flag
        this.loginType = true
        this.disabled = false
        this.verifyFlag = false
        this.time = 60
        this.timeCode = "获取验证码"
        this.code = ""
      },
      forget() {
        this.loginType = !this.loginType
      },
      getCode() {
        if(this.phone == '') {
          this.$Notice.error({
            title: '请输入手机号',
          })
          return
        }
        if (this.time > 0) {
          if (this.time == 60) {
            var qs = require('qs')
            var self = this
            this.$http.post(PRIFIX + '/api/user/checkPhoneToCode', qs.stringify({
              'phone': this.phone,
              'type': 1
            })).then(function (response) {
              response = response.data
              if (response.code === 0) {
                self.$Notice.success({
                  title: '验证码已发送',
                })
              } else {
                self.$Notice.error({
                  title: '验证码发送失败',
                })
              }
            }).catch(function (error) {
            })
          }
          this.time--
          this.timeCode = this.time + "s 后重新获取"
          this.disabled = true
          setTimeout(this.getCode, 1000)
        } else {
          this.time = 60
          this.disabled = false
          this.timeCode = "获取验证码"
        }
      },
      reset() {
        var qs = require('qs')
        var self = this
        this.$http.post(PRIFIX + '/api/user/checkCode', qs.stringify({
          'code': self.codeForget
        })).then(function (response) {
          response = response.data
          if (response.code == 0) {
            if (self.verifyFlag) {
              self.forgetPassword = !self.forgetPassword
            }
          } else {
            self.$Notice.error({
              title: response.message
            })
          }
        }).catch(function (error) {
        })
      },
      OK() {
        if (this.password2 == "") {
          return this.$Notice.error({
            title: response.message
          })

        } else {
          if (this.password2 == this.password3) {
            var qs = require('qs')
            var self = this;
            this.$http.post(PRIFIX + '/api/user/forgetPassword', qs.stringify({
              'phone': this.phone,
              'code': this.codeForget,
              'password': this.password3
            })).then(function (response) {
              response = response.data
              if (response.code == 0) {
                /*清空数据*/
                self.inputType = "password"
                self.inputFlag = true
                self.forgetPassword = true
                self.flag = true
                self.loginType = true
                self.disabled = false
                self.verifyFlag = false
                self.time = 60
                self.timeCode = "获取验证码"
                self.password = this.password2
                self.code = ""
                self.password2 = ""
                self.password3 = ""
                self.password4 = ""
              } else {
                self.$Notice.error({
                  title: response.message
                })
              }
            }).catch(function (error) {
            })
          } else {
            return this.$Notice.error({
              title: '2次密码不一致！'
            })
          }
        }
      },
      //验证输入
      verify() {
        var reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/
        if (this.flag) {
          /*登录*/
          /*判断手机号*/
          if (this.phone == '') {
            this.$Notice.error({
              title: '请输入手机号码！'
            })
          } else if (!reg.test(this.phone)) {
            this.$Notice.error({
              title: '手机格式不正确！'
            })
          } else {
            if (!this.loginType) {
              /*判断验证码*/
              if (this.code == "") {
                this.$Notice.error({
                  title: '请输入验证码！'
                })
              } else {
                this.verifyFlag = true
                /*判断验证码是否正确*/
                return true
              }
            }
            if (this.loginType) {
              /*判断密码*/
              if (this.password == "") {
                this.$Notice.error({
                  title: '请输入密码！'
                })
              } else {
                this.verifyFlag = true
                /*判断密码是否正确*/
                return true
              }
            }
          }
        } else {
          /*注册*/
          /*判断手机号*/
          if (this.phone == '') {
            this.$Notice.error({
              title: '请输入手机号码！'
            })
          } else if (!reg.test(this.phone)) {
            this.$Notice.error({
              title: '手机格式不正确！'
            })
          } else {
            /*判断验证码*/
            if (this.codeRegister == "") {
              this.$Notice.error({
                title: '请输入验证码！'
              })
            } else {
              if (this.password4 == "") {
                this.$Notice.error({
                  title: '请输入密码！'
                })
              } else {
                this.verifyFlag = true
                /*判断验证码是否正确*/
                return true
              }
            }
          }
        }

      },
      //注册
      register() {
        var qs = require('qs')
        var self = this;
        this.$http.post(PRIFIX + '/api/user/register', qs.stringify({
          'phone': this.phone,
          'code': this.codeRegister,
          'password': this.password4
        })).then(function (response) {
          response = response.data
          if (response.code == 0) {

            self.$Notice.success({
              title: '注册成功',
            })

            /*清空数据*/
            self.inputType = "password"
            self.inputFlag = true
            self.forgetPassword = true
            self.flag = true
            self.loginType = true
            self.disabled = false
            self.verifyFlag = false
            self.time = 60
            self.timeCode = "获取验证码"
            self.password = this.password2
            self.code = ""
            self.password2 = ""
            self.password3 = ""
            self.password4 = ""
          } else {
            self.$Notice.error({
              title: response.message
            })
          }
        }).catch(function (error) {
        })
      }
    },
  }

</script>

<style lang="less" scoped>
  .loginWrapper {
    position: fixed;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    overflow: hidden;
  }

  .login_bg {
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    z-index: -1;
  }

  .loginCardWrapper {
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    /*排列方式 垂直*/
    -webkit-box-orient: vertical;
    /*是否反方向显示*/
    -webkit-box-direction: normal;
    /*排列方式 列*/
    -ms-flex-direction: column;
    flex-direction: column;
    /*对齐方式 居中 配合flex-pack使用上下左右居中对齐*/
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    /*对齐方式 居中*/
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    /*内容对齐 居中不留空隙*/
    justify-content: center;
    border-radius: 2px;
    min-height: 688px;
    /*calc()函数。这里的意思是设置宽度比100%的宽度少42px。*/
    height: calc(100% - 42px);
    /*并列显示*/
    box-sizing: border-box;
  }

  .loginCard {
    width: 432px;
    height: 515px;
  }

  .loginCard h2 {
    color: #ed4040;
    text-align: center;
    margin-top: 20px;
  }

  .logo {
    width: 244px;
    height: 54px;
    margin: 0 auto;
  }

  .logo img {
    width: 256px;
    height: 74px;
  }

  .logo img:hover {
    cursor: pointer;
  }

  .inputWrapper {
    margin-top: 30px;
  }

  .inputContent {
    position: relative;
    margin: 15px 0px;
  }

  .inputCss {
    border: none;
    padding: 5px;
    font-size: 16px;
    border-bottom: 1px #ebebeb solid;
    outline: none;
    width: 398px;
    height: 48px;
  }

  .inputIcon {
    position: absolute;
    right: 0px;
    top: 13px;
    font-size: 22px;
  }

  .inputHover:hover {
    cursor: pointer;
  }

  .inputButton {
    position: absolute;
    right: 0px;
    top: 8px;
  }

  .description {
    margin-top: 20px;
  }

  .phoneWay {
    display: inline-block;
  }

  .phoneWay a {
    color: #175199;;
  }

  .forget a {
    color: #8590a6;
  }

  .loginButton button {
    margin-top: 20px;
    width: 100%;
    font-size: 16px;
  }

  .otherLogin {
    margin-top: 15px;
  }

  .title {
    margin-bottom: 10px;
  }

  .other_login_icon {
    display: inline-block;
    margin-right: 30px;
    text-align: center;
  }

  .other_login_icon img {
    width: 40px;
    height: 40px;
  }

  .other_login_icon:hover {
    cursor: pointer;
    color: #ed3f14;
  }

  .register {
    position: relative;
    margin-top: 5px;
  }

  .rightWrapper {
    float: right;
  }

  .rightWrapper a {
    color: #2d8cf0;
  }

  .login {
    margin-top: 55px;
  }

  /*.move-enter-active, .move-enter-active {
    transition: all 0.2s;
  }
  .move-enter, .move-leave-to {
    opacity: 0;
  }*/

</style>



